import { FC, useEffect, useState } from "react";
const isInStandaloneMode = () => 
  window.matchMedia("(display-mode: standalone)").matches ||
  (window.navigator as any)?.standalone ||
  document.referrer.includes("android-app://");

type AppProps = {};

const App: FC<AppProps> = () => {
  const [deferredPrompt, setDeferredPrompt] = useState<any>();

  useEffect(() => {
    const eventName = "beforeinstallprompt";

    const onBeforeInstallPrompt = (e: any) => {
      // Prevent the mini-infobar from appearing on mobile
      e.preventDefault();
      // Stash the event so it can be triggered later.

      setDeferredPrompt(e);

      alert(eventName);
    };

    window.addEventListener(eventName, onBeforeInstallPrompt);

    return () => window.removeEventListener(eventName, onBeforeInstallPrompt);
  }, []);

  const isStandaloneIos =
    "standalone" in window.navigator && !(window.navigator as any).standalone;

  const isStandaloneAndroid = !window.matchMedia("(display-mode: standalone)")
    .matches;

  const isStandalone = !!(isStandaloneIos || isStandaloneAndroid);

  return (
    <div>
      <div>
        deferredPrompt:
        {!!deferredPrompt ? "truthy" : "falsy"}
      </div>
      <button
        onClick={() => {
          if (!deferredPrompt) {
            alert("deferredPrompt was falsy...");
          }

          deferredPrompt.prompt();
          deferredPrompt.userChoice.then((choiceResult: any) => {
            if (choiceResult.outcome === "accepted") {
              alert("User accepted the install prompt");
            } else {
              alert("User dismissed the install prompt");
            }
          });
        }}
      >
        show install prompt
      </button>

      <div>is standalone : {isStandalone + ''}</div>
      <div>is standalone ios: {isStandaloneIos+ ''}</div>
      <div>is standalone android: {isStandaloneAndroid+ ''}</div>

      <div> is standalone v2: {isInStandaloneMode()+ ''} </div>
    </div>
  );
};

export default App;
